.banner-section{
    background-image: url("./../Images/Banner.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.banner-section .row{
    align-items: center;
}
.banner-section h2{
    font-size: 45px;
    color: #221538;
    font-weight: bold;
}
.banner-section p{
    color: rgb(111, 112, 116);
}
.banner-section img{
    width: 95%;
}
.banner-section button{
    padding: 10px 30px;
    color: #fff;
    background: var(--cream);
    border:1px solid var(--cream) ;
    margin-right: 8px;
    font-size: 18px;
    font-weight: 600;
}
.banner-section button:hover{
    background: transparent;
    border: 1px solid var(--cream);
    color:var(--cream);
}
.banner-section button:last-child{
    color: white;
    background: var(--dark);
    border: 1px solid var(--dark);
}
.banner-section button:last-child:hover{
    background: transparent;
    color: var(--dark);
    border: 1px solid var(--dark);
}

