.count-section{
    background:linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("./../Images/counterSectionBg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 80px 0;
    color: #fff;
    display: flex;
    align-items: center;
}

.bgoverlay{
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
}